import "core-js/modules/web.url-search-params.delete.js";
import "core-js/modules/web.url-search-params.has.js";
import "core-js/modules/web.url-search-params.size.js";
import fetchWithAuth from '@/utils/fetchWithAuth';
import packageInfo from '../../package.json';
export default {
  name: 'SysCogStatus',
  data() {
    return {
      loading: false,
      rebuilding: false,
      backing: false,
      restoring: false,
      indexInfo: {},
      version: packageInfo.version // 从package.json获取版本号
    };
  },
  mounted() {
    this.fetchIndexInfo();
  },
  methods: {
    // 获取索引信息
    async fetchIndexInfo() {
      this.loading = true;
      try {
        const response = await fetchWithAuth('/api/manage/list?action=info', {
          method: 'GET'
        });
        if (response.ok) {
          const data = await response.json();
          this.indexInfo = data;
        } else {
          throw new Error('API请求失败');
        }
      } catch (error) {
        console.error('获取索引信息失败:', error);
        this.$message.error('获取索引信息失败');
      } finally {
        this.loading = false;
      }
    },
    // 重建索引
    async rebuildIndex() {
      this.rebuilding = true;
      try {
        const response = await fetchWithAuth('/api/manage/list?action=rebuild', {
          method: 'GET'
        });
        if (response.ok) {
          this.$message.success('索引重建已启动，请稍后刷新查看最新状态');
          // 延迟刷新数据
          setTimeout(() => {
            this.fetchIndexInfo();
          }, 3000);
        } else {
          throw new Error('API请求失败');
        }
      } catch (error) {
        console.error('重建索引失败:', error);
        this.$message.error('重建索引失败');
      } finally {
        this.rebuilding = false;
      }
    },
    // 备份数据
    async backupData() {
      this.backing = true;
      try {
        const response = await fetchWithAuth('/api/manage/sysConfig/backup?action=backup', {
          method: 'GET'
        });
        if (response.ok) {
          // 创建下载链接
          const blob = await response.blob();
          const url = window.URL.createObjectURL(blob);
          const a = document.createElement('a');
          a.href = url;
          a.download = `imgbed_backup_${new Date().toISOString().split('T')[0]}.json`;
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
          window.URL.revokeObjectURL(url);
          this.$message.success('备份文件已下载');
        } else {
          const errorData = await response.json();
          throw new Error(errorData.error || 'API请求失败');
        }
      } catch (error) {
        console.error('备份数据失败:', error);
        this.$message.error('备份数据失败: ' + error.message);
      } finally {
        this.backing = false;
      }
    },
    // 选择恢复文件
    selectRestoreFile() {
      if (this.restoring) return;
      this.$refs.fileInput.click();
    },
    // 处理文件选择
    async handleFileSelect(event) {
      const file = event.target.files[0];
      if (!file) return;
      if (!file.name.endsWith('.json')) {
        this.$message.error('请选择JSON格式的备份文件');
        return;
      }

      // 确认恢复操作
      try {
        await this.$confirm('恢复操作将覆盖现有的文件元数据和系统设置，此操作不可逆。确定要继续吗？', '确认恢复', {
          confirmButtonText: '确定恢复',
          cancelButtonText: '取消',
          type: 'warning'
        });
        await this.restoreData(file);
      } catch (error) {
        if (error !== 'cancel') {
          console.error('确认恢复失败:', error);
        }
      }

      // 清除文件选择
      event.target.value = '';
    },
    // 恢复数据
    async restoreData(file) {
      this.restoring = true;
      try {
        const response = await fetchWithAuth('/api/manage/sysConfig/backup?action=restore', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: await file.text()
        });
        if (response.ok) {
          const result = await response.json();
          this.$message.success(`恢复完成！已恢复 ${result.stats.restoredFiles} 个文件和 ${result.stats.restoredSettings} 个设置项`);
          // 刷新索引信息
          setTimeout(() => {
            this.fetchIndexInfo();
          }, 1000);
        } else {
          const errorData = await response.json();
          throw new Error(errorData.error || 'API请求失败');
        }
      } catch (error) {
        console.error('恢复数据失败:', error);
        this.$message.error('恢复数据失败: ' + error.message);
      } finally {
        this.restoring = false;
      }
    },
    // 格式化时间
    formatTime(timestamp) {
      if (!timestamp) return '未知';
      const date = new Date(timestamp);
      return date.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      });
    },
    // 计算百分比
    getPercentage(value, total) {
      if (!total || total === 0) return 0;
      return Math.round(value / total * 100);
    },
    // 计算时间差
    getTimeAgo(timestamp) {
      if (!timestamp) return '';
      const now = Date.now();
      const diff = now - timestamp;
      const minutes = Math.floor(diff / 60000);
      const hours = Math.floor(diff / 3600000);
      const days = Math.floor(diff / 86400000);
      if (days > 0) return `${days}天前`;
      if (hours > 0) return `${hours}小时前`;
      if (minutes > 0) return `${minutes}分钟前`;
      return '刚刚';
    }
  }
};